import Link from "next/link";

type AuthShellProps = {
  title: string;
  subtitle: string;
  children: React.ReactNode;
  footer?: React.ReactNode;
};

export function AuthShell({ title, subtitle, children, footer }: AuthShellProps) {
  return (
    <main className="mx-auto flex min-h-[calc(100vh-65px)] w-full max-w-5xl items-center px-6 py-10">
      <div className="grid w-full gap-6 md:grid-cols-2">
        <section className="kv-surface rounded-3xl p-8">
          <p className="kv-pill inline-flex">KoreVitta</p>
          <h1 className="mt-3 text-3xl font-semibold tracking-tight">{title}</h1>
          <p className="mt-2 text-sm text-muted-foreground">{subtitle}</p>
          <ul className="mt-6 space-y-2 text-sm text-muted-foreground">
            <li>• Simplicidade acima de tudo</li>
            <li>• Fluxo orientado à retenção</li>
            <li>• Clareza diária para paciente</li>
            <li>• Eficiência operacional para profissional</li>
          </ul>
        </section>
        <section className="kv-surface rounded-3xl p-8">
          {children}
          {footer ? <div className="mt-6 text-sm text-muted-foreground">{footer}</div> : null}
          <div className="mt-4 text-sm text-muted-foreground">
            <Link href="/" className="underline underline-offset-4">
              Voltar para a landing
            </Link>
          </div>
        </section>
      </div>
    </main>
  );
}
